nav{
  /* position: absolute; */
  

padding: 10px;
display: flex;
flex-grow: 1;
justify-content: space-evenly;
align-items: center;
animation: faidin 1.5s   forwards;
opacity: 0;
/* float:right; */
}
@keyframes faidin{
  100%{
    opacity: 1;
  }
}

nav .nev-items{
  
 display: flex;
 /* justify-content: flex-end;
