*{
margin: 0;
}
html{
font: normal 16px san-serif;
}
.App{
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex-grow: 1;
}
header{
  position: absolute;
  width: 100%;
  z-index: 2;
}
section{
  /* padding-top: 50px; */
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.aa{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: easeIn 1s  forwards cubic-bezier(0.4, 0, 0.2, 1) ;
  transform: scale(0) ;
  transform-origin: bottom;
  
}
@keyframes easeIn{
  100%{
    transform: scale(1);
   
  }
}
.btn{
font-size: 20px;
text-align: center;
border: none;
border-radius: 4px;
padding:10px;
font-weight: 700;
background-color: rgb(111, 201, 150);
box-shadow: 2px 2px 15px rgba(14, 14, 14, 0.267) ;
/* justify-content: center; */
}

.btn:active{
box-shadow: 2px 2px 5px rgba(14, 14, 14, 0.267) ;

}

.bg-img{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -5;
  background-size: cover;
  /* background-image: url(./21445119.jpg); */
  /* background-image: url(https://www.google.com/url?sa=i&url=https%3A%2F%2Fpixabay.com%2Fimages%2Fsearch%2Fbackground%2520image%2F&psig=AOvVaw2pZCaRKLWc6EBWbVUC3sSw&ust=1584256872047000&source=images&cd=vfe&ved=0CAIQjRxqFwoTCNCL9va2megCFQAAAAAdAAAAABAU"); */
background-color: seagreen;
}

footer{
  background-color: rgb(18, 95, 51);
  width: 100%;
  height: 10vh;
  text-align: center;
  font-weight: 700;
}

.osection{
  background: linear-gradient(180deg, seagreen, #e0820da3);

}